import React from 'react';
import PropTypes from 'prop-types';

const WelcomeScreen = ({gameTime, errorCount, playClickHandler}) => {
  // const {gameTime, errorCount, playClickHandler} = props;
  return (
    <section className="welcome">
      <div className="welcome__logo">
        <img src="img/melody-logo.png" alt=" " width="186" height="83" />
      </div>
      <button className="welcome__button" onClick={playClickHandler}>
        <span className="visually-hidden"> </span>
      </button>
      <h2 className="welcome__rules-title"> </h2>
      <p className="welcome__text"> :</p>
      <ul className="welcome__rules-list">
        <li>{gameTime}    .</li>
        <li>  {errorCount} .</li>
      </ul>
      <p className="welcome__text">!</p>
    </section>
  );
};

WelcomeScreen.propTypes = {
  gameTime: PropTypes.number.isRequired,
  errorCount: PropTypes.number.isRequired,
  playClickHandler: PropTypes.func,
};

export default WelcomeScreen;